.main-container{
    width: 100%;
    text-align: center; 

}

.main-container #Row{
    margin-top: 30px;
}

.main-container .logo{
    width: 250px;
}

.main-container #Row #Col img{
    max-width: 100%;
    border-radius: 5px 5px 0 0;
}

.main-container #Row #Col{
    display: flex;
    flex-direction: column;
    border: 2px solid  rgba(0, 0, 0, 0.2);
    border-radius: 5px 5px 0 0;
    margin-bottom: 30px;
}

.main-container #Row #Col footer{
    flex: 1;
    height: 100px;
    background-color: #fff;
    border: 1px solid #eee;
    padding: 15px 20px;
    text-align: left;
    border-radius: 0 0 5px 5px;
}

.main-container #Row #Col footer strong{
    font-size: 16px;
    color: #333;
}

.main-container #Row #Col footer p {
    font-size: 14px;
    line-height: 20px;
    color:#999;
    margin-top: 5px;
}

.main-container #Row #Col  .buttons{
    margin-top: -10px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 10px;

}

.main-container #Row #Col  .buttons button{
    height: 50px;
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.05);
    border: 0;
    border-radius: 4px;
    background: #fff;
    cursor: pointer;
}

.main-container #Row #Col  .buttons button:hover{
    transform: translateY(-5px);
    transition: all .2s;
}

.match-container{
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: rgba(0, 0, 0, 0.8);
}

.main-container .avatar{
    width: 200px;
    height: 200px;
    border-radius: 50%;
    border: 5px solid #fff;
}

.main-container strong{
    font-size: 32px;
    color: #fff;
}

.main-container p{
    margin-top: 10px;
    font-size: 20px;
    line-height: 30px;
    max-width: 400px;
    color:    rgba(255, 255, 255, 0.8);
}

.main-container button{
    border: 0px;
    background: none;
    font-weight: none;
    color: rgba(255, 255, 255, 0.8);
    font-size: 18px;
    margin-top: 30px;
    cursor: pointer;
}