.expla {
    opacity: 0.8;
    height: 100%;
    width: 50%;
}


.collum{
    display: flex;
    width: 100%;
    flex: 1;
    justify-content: center;
    align-items: center;
    
    flex-direction: row;
}

.titulo {
    height: 100%;
    width: 100%;
    font-size: 3.0rem;
    font-weight: 300;
    line-height: 1.2;
}

p{
    font-family: Arial, Helvetica, sans-serif;
}

.texto {
    width: 70%;
    height: auto;
    color: black;
    text-align: center;
}

p{
    font-family: inherit;
    color: inherit;
    font-size: 2rem;
    font-weight: 300;
    line-height: 1.2;
    margin-bottom: 30px;
}

h2 {
    font-size: 2.5rem;
    font-weight: bolder;
    line-height: 1.2;
    margin-bottom: 0.5rem;
    font-family: inherit;
    color: inherit;
  }

  
h1 {
    font-size: 3rem;
    font-weight: 300;
    line-height: 1.2;
    margin-bottom: 30px;
  }

.container{
    display: flex;
    width: 100%;
    flex: 1;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }

.logo{
    width: 200px;
}

.logoo{
    width: 50px;
}

.inicio{
    width: 100%;
    display: flex;
    flex: 1;
    background-color: white;
    justify-content: center;
    align-items: center;
    margin-bottom: 30px;
    border-bottom: 2px solid rgba(0, 0, 0, 0.5);
}

